import Page from 'components/Page';
import APICon from '../TestData/ServerAddress.json';
import React from 'react';

import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import Loader from 'react-loader-spinner'
var APIBasePath =APICon.APIURL;
var ReportPublishPath =APICon.ReportURL;
function addSpinner()
{
  return(
    <Loader
       type="Puff"
       color="#00BFFF"
       height={50}
       width={10000}
       timeout={1200000}

    />
   );
}
function GetAllModule({ options }) {

  return (
    Object.keys(options).map((key)  => 
    <option >{options[key]}</option>)
  );
  
}
function CheckValueFromJson( options,checkvalue) 
{
  let allitem = []
     Object.keys(options).map((key) => (
      allitem.push(options[key])
    ));
    for (let i = 0; i < allitem.length; i++) 
    {
      if(allitem[i].trim().toLowerCase()===checkvalue.trim().toLowerCase())
      {
        return true;
      }
     
    }
    return false;
  
}

class TestAttribute extends React.Component{
  
  constructor(props){
    super(props);
    async function apiGetAll (oldName,requestOptions) {
      try {
        const resp = await fetch(APIBasePath+'testcase?ExistingModuleName='+oldName,requestOptions)
        console.log(resp)
        return resp
      } catch (err) {
           console.log(err)
        }
   }
    this.state={
      ModuleModuleName:'',
      NewModuleName:'',
      allModuleName: [],
      ModuleName:'',
      TestID: [],
      TestCaseID:'',
      testCaseName:'',
      NewTestID:'',
      isLoaded: false,
      modal: false,
      modalValidationText:'',
      newmoduleNameCheck: false,
      existingModuleName:false,
      testAttributeModuleNameCheck:false,
      testAttributeTestIDcheck:false,
      testAttributeNewTestIDcheck:false,
      testAttributeNewTestCaseName:false,
      postResponceresult:[],

    }
    fetch(APIBasePath+'testcase?moduleName=')
    .then(res=>res.json())
    .then(json=> {
      this.setState({
        allModuleName:json,
      }
      )
    });
    this.setState({isLoaded:true})


    
  }

  SelectModule(event){
    this.setState({isLoaded:false})
    this.setState({ModuleModuleName : event.target.value})
    this.setState({existingModuleName:false})
    this.setState({isLoaded:true})

    }
    UpdateModuleName(event){
      this.setState({isLoaded:false})
      this.setState({NewModuleName : event.target.value})
      this.setState({newmoduleNameCheck:false})
      this.setState({isLoaded:true})
      }

      UpdateNewTestCaseID(event){
        this.setState({isLoaded:false})
        this.setState({NewTestID : event.target.value})
        this.setState({testAttributeNewTestIDcheck:false})
        this.setState({isLoaded:true})
        }
        UpdateNewTestCaseName(event){
          this.setState({isLoaded:false})
          this.setState({testCaseName : event.target.value})
          this.setState({testAttributeNewTestCaseName:false})
          this.setState({isLoaded:true})
          }

  updateModule(event){
    this.setState({isLoaded:false})
    this.setState({TestID : ''})
    this.setState({TestCaseID : ''})
    this.setState({NewTestID : ''})
    this.setState({testCaseName : ''})
    this.setState({ModuleName : event.target.value})
      fetch(APIBasePath+'testcase?moduleName='+event.target.value)
      .then(res=>res.json())
      .then(json=> {
        this.setState({
          TestID:json,
        }
        )
      });
      this.setState({testAttributeModuleNameCheck:false})
      this.setState({isLoaded:true})

    }
    updateTestID(event){
      this.setState({isLoaded:false})
      this.setState({TestCaseName : ''})
      this.setState({NewTestID : event.target.value})
      this.setState({TestCaseID : event.target.value})
      fetch(APIBasePath+'testcase?moduleName='+this.state.ModuleName+'&testid='+event.target.value)
      .then(res=>res.json())
      .then(json=> {
        this.setState({
          testCaseName:json.TestCaseName,
        }
        )
      });
      this.setState({testAttributeTestIDcheck:false})
      this.setState({testAttributeNewTestIDcheck:false})
      this.setState({testAttributeNewTestCaseName:false})
      this.setState({isLoaded:false})
      }
  SaveModuleName()
  {
    var validationMessageofPost='';
    this.setState({existingModuleName:false})
    this.setState({newmoduleNameCheck:false})
    this.setState({postResponceresult:''})
    var oldName = this.state.ModuleModuleName;
    var NewName = this.state.NewModuleName;
    var ExceptionMessage ='';
    if(oldName.trim()==="")
    {
      this.setState({existingModuleName:true})
      ExceptionMessage= ExceptionMessage+ 'Module name can not be left blank.'
    }
    if(NewName.trim()==="")
    {
      this.setState({newmoduleNameCheck:true})
      ExceptionMessage= ExceptionMessage+ 'New Module name can not be left blank.'
    }
    if(ExceptionMessage.trim() !=="")
    {
      this.setState({modal:true})
      return this.setState({modalValidationText:ExceptionMessage})
    }
    var duplicatecheck = CheckValueFromJson(this.state.allModuleName,NewName)
    if(duplicatecheck)
    {
      this.setState({modal:true})
      this.setState({newmoduleNameCheck:true})
      return this.setState({modalValidationText:'New Module name is already exist.'})
    }
    else
    {
      var  testdata=JSON.stringify({newModuleName:NewName});
      var requestOptions = {
        method: 'POST',
        //mode: 'no-cors',
        headers: {"Accept": "application/json; odata=verbose",'Content-type': 'application/x-www-form-urlencoded'},
       // headers: {'Accept': 'application/json; odata=verbose','Content-type': 'application/x-www-form-urlencoded','Access-Control-Allow-Origin': '*','Access-Control-Allow-Credentials': 'true' },
       // headers: {'Accept': 'application/json','Content-type': 'application/json; charset=utf-8' },
        body: testdata
    };
     fetch(APIBasePath+'testcase?ExistingModuleName='+oldName,requestOptions)
     .then(function(response) {
             return response.json();
         })
      .then(jsonStr => {
       this.setState({ postResponceresult: jsonStr });
       validationMessageofPost=jsonStr.success;
       if(validationMessageofPost.toString().toLowerCase()==='true')
       {
         fetch(APIBasePath+'testcase?moduleName=')
         .then(res=>res.json())
         .then(json=> {
           this.setState({
             allModuleName:json,
           }
           )
         });
         this.setState({ModuleModuleName:''})
         this.setState({NewModuleName:''})
         this.setState({modal:true})
         return this.setState({modalValidationText:this.state.postResponceresult.servermessage})
       }
       else
       {
         this.setState({modal:true})
         return this.setState({modalValidationText:'ISSUE:'+this.state.postResponceresult.servermessage})
       }
     })

  }
   
  }
  SaveTestAttribute()
  {
    var validationMessageofPost='';
    var ExceptionMessage ='';
    var testAttributeModule= this.state.ModuleName
    var testAttributeTestID= this.state.TestCaseID
    var testAttributeNewTestID= this.state.NewTestID
    var testAttributeName = this.state.testCaseName
    if(testAttributeModule.trim()==="")
    {
      this.setState({testAttributeModuleNameCheck:true})
      ExceptionMessage= ExceptionMessage+ 'Module* can not be left blank.'
    }
    if(testAttributeTestID.trim()==="")
    {
      this.setState({testAttributeTestIDcheck:true})
      ExceptionMessage= ExceptionMessage+ 'TestID* can not be left blank.'
    }
    if(testAttributeNewTestID.trim()==="")
    {
      this.setState({testAttributeNewTestIDcheck:true})
      ExceptionMessage= ExceptionMessage+ 'New TestID* can not be left blank.'
    }
    if(testAttributeName.trim()==="")
    {
      this.setState({testAttributeNewTestCaseName:true})
      ExceptionMessage= ExceptionMessage+ 'New Test Case Name* can not be left blank.'
    }
    if(ExceptionMessage.trim() !=="")
    {
      this.setState({modal:true})
      return this.setState({modalValidationText:ExceptionMessage})
    }
    if(testAttributeTestID !==testAttributeNewTestID)
    {
      var duplicatecheck = CheckValueFromJson(this.state.TestID,testAttributeNewTestID)
      if(duplicatecheck)
      {
        this.setState({modal:true})
        this.setState({testAttributeNewTestIDcheck:true})
        return this.setState({modalValidationText:'New TestID* is already exist.'})
      }
    }
      
      var  testdata=JSON.stringify({newTestID:testAttributeNewTestID,newTestCaseName:testAttributeName});
      var requestOptions = {
        method: 'POST',
       // mode: 'no-cors',
       headers: {"Accept": "application/json; odata=verbose",'Content-type': 'application/x-www-form-urlencoded'},
        body: testdata
    };
    fetch(APIBasePath+'testcase?ExistingModuleName='+testAttributeModule+'&existingTestID='+testAttributeTestID,requestOptions)
    .then(function(response) {
      return response.json();
  })
  .then(jsonStr => {
  this.setState({ postResponceresult: jsonStr });
  validationMessageofPost=jsonStr.success;
  console.log(jsonStr);
  console.log(validationMessageofPost);
  if(validationMessageofPost.toString().toLowerCase()==='true')
  {
      this.setState({ModuleName:''})
      this.setState({TestCaseID:''})
      this.setState({NewTestID:''})
      this.setState({testCaseName:''})
      this.setState({modal:true})
      return this.setState({modalValidationText:this.state.postResponceresult.servermessage})
  }
  else
  {
    this.setState({modal:true})
    return this.setState({modalValidationText:'ISSUE: '+this.state.postResponceresult.servermessage})
  }
  })
   
  }
  toggle = modalType => () => {
    if (!modalType) {
      return this.setState({
        modal: !this.state.modal,
      });
    }

    this.setState({
      [`modal_${modalType}`]: !this.state[`modal_${modalType}`],
    });
  };


 
  render() {

  var {isLoaded,allModuleName,TestID} = this.state;

  if(!isLoaded)
  {
    addSpinner();
  }

  return (
    <Page title="Test Attribute" breadcrumbs={[{ name: 'Test Attribute', active: true }]}>
      <Row>
        <Col lg={6} md={6} sm={6} xs={12}>
          <Card>
            <CardHeader>Update Module Name</CardHeader>
            <CardBody>
              <Form>
                <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    Module*
                  </Label>
                  <Col>
                    <Input  invalid={this.state.existingModuleName} type="select" name="module"  value={this.state.ModuleModuleName} onChange={this.SelectModule.bind(this)}>
                    <option selected="selected">{this.state.ModuleModuleName}</option>
                     <GetAllModule options={allModuleName} />
					          </Input>
                  </Col>
                </FormGroup>
				          <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    New Module Name*
                  </Label>
                  <Col >
                    <Input invalid={this.state.newmoduleNameCheck} type="input" name="newmoduleName" placeholder="enter new module name" value={this.state.NewModuleName} onChange={this.UpdateModuleName.bind(this)}>
					          </Input>
                  </Col>
                </FormGroup>
				         <FormGroup col>
                  <Col >
					          <Button onClick={this.SaveModuleName.bind(this)} color="primary">Update Module Name
                    </Button>
                    <Modal
                      isOpen={this.state.modal}
                      toggle={this.toggle()}
                      className={this.props.className}>
                      <ModalHeader  toggle={this.toggle()}> Information</ModalHeader>
                      <ModalBody >
                        {this.state.modalValidationText}
                      </ModalBody>
                      <ModalFooter>
                        <Button color="secondary" onClick={this.toggle()}>
                          OK
                        </Button>
                      </ModalFooter>
                    </Modal>
                  </Col>
                </FormGroup>
              </Form>
            </CardBody>
          </Card>
        </Col>
		 <Col lg={6} md={6} sm={6} xs={12}>
          <Card>
            <CardHeader>Update TestID/Test Case Name</CardHeader>
            <CardBody>
              <Form>
                <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    Module*
                  </Label>
                  <Col>
                    <Input  invalid={this.state.testAttributeModuleNameCheck} type="select" name="moduleforTestUpdate" value={this.state.ModuleName} onChange={this.updateModule.bind(this)}>
                    <option selected="selected">{this.state.ModuleName}</option>
                    <GetAllModule options={allModuleName} />
					          </Input>
                  </Col>
                </FormGroup>
				        <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    TestID*
                  </Label>
                  <Col>
                    <Input invalid={this.state.testAttributeTestIDcheck} type="select" name="testID" value={this.state.TestCaseID} onChange={this.updateTestID.bind(this)}>
				            	<option selected="selected">{this.state.TestCaseID}</option>
                      <GetAllModule options={TestID} />
					          </Input>
                  </Col>
                </FormGroup>
				        <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    New TestID*
                  </Label>
                  <Col >
                    <Input type="input" invalid={this.state.testAttributeNewTestIDcheck} name="newtestcaseid" placeholder="enter new test case ID"  value={this.state.NewTestID} onChange={this.UpdateNewTestCaseID.bind(this)}>
					           </Input>
                  </Col>
                </FormGroup>
				        <FormGroup col>
                  <Label for="exampleSelect" sm={6}>
                    New Test Case Name*
                  </Label>
                  <Col >
                    <Input type="input" invalid={this.state.testAttributeNewTestCaseName} name="testcasename" placeholder="enter new test case name"  value={this.state.testCaseName} onChange={this.UpdateNewTestCaseName.bind(this)}>
					         </Input>
                  </Col>
                </FormGroup>
			          	<FormGroup col>
                  <Col >
					         <Button onClick={this.SaveTestAttribute.bind(this)} color="primary">Update Test Attribute</Button>
                  </Col>
                </FormGroup>
              </Form>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Page>
  );
  }
};

export default TestAttribute;
